import { Component, useState } from "react";
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
} from 'react-router-dom';
import Teams from "./Teams";
export default function Navbar() {
    const [navbar, setNavbar] = useState(false);
    const [colorChange, setColorchange] = useState(false);

    const changeNavbarColor = () => {

        if (window.location.pathname === "/teams" || window.location.pathname === "/Contact") {
            setColorchange(true);
        }
        else {
            if (window.scrollY > 680 ) {
                setColorchange(true);
            }
            else {
                setColorchange(false);
            }
        };
    }
    

    window.addEventListener('scroll', changeNavbarColor);
    return (
        <nav id="Navbar" className={colorChange ? 'navbar colorChange' : 'navbar'}   >
            <div className="justify-between px-4 mx-auto lg:max-w-7xl md:items-center md:flex md:px-8">
                <div>
                    <div className="flex items-center justify-between py-3 md:py-5 md:block">
                        <Link to=""><h2 className="text-2xl font-q1 text-red-400 ">YAPAY ZEKA TOPLULUU</h2>  </Link>



                        <div className="md:hidden">
                            <button
                                className="p-2 text-gray-700 rounded-md outline-none focus:border-gray-400 focus:border"
                                onClick={() => setNavbar(!navbar)}
                            >
                                {navbar ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        className="w-6 h-6"
                                        viewBox="0 0 20 20"
                                        fill="currentColor"
                                    >
                                        <path
                                            fillRule="evenodd"
                                            d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                            clipRule="evenodd"
                                        />
                                    </svg>
                                ) : (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        className="w-6 h-6"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        stroke="currentColor"
                                        strokeWidth={2}
                                    >
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            d="M4 6h16M4 12h16M4 18h16"
                                        />
                                    </svg>
                                )}
                            </button>
                        </div>
                    </div>
                </div>
                <div>
                    <div
                        className={`flex-1 justify-self-center pb-3  mt-8 md:block md:pb-0 md:mt-0 ${navbar ? "block" : "hidden"
                            }`}
                    >
                        <ul className="items-center justify-center space-y-8 md:flex md:space-x-6 md:space-y-0">
                            <li className="text-color1 hover:text-white hover:bg-color1  hover:rounded-full  ">
                                <a href="/Home">ANASAYFA</a>
                            </li>

                            <li className="text-color1 hover:text-white hover:bg-color1  hover:rounded-full">
                                <a href="/Contact">LETM</a>
                            </li>
                            <li className="text-color1 hover:text-white hover:bg-color1  hover:rounded-full  ">
                                <a href="/teams">EKP</a>
                            </li>
                            <li className="bg-color1 hover:bg-purple-700 text-white font-bold py-2 px-4 rounded-full ">
                                <a href="#">KAYIT OL/GR YAP</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>

        </nav>

    );
}